/* 헤더 */
body{position: fixed; width: 100%; margin: 0; overflow: scroll;}
.header{z-index: 2; position: fixed; top: 0; left: 0; width: 100%; height:65px;background-color: #a1c0d6;  color: #000;}
.header_top{padding: 5px 10px 10px 10px; font-size: 12px;}
.header_top,.header_bottom{display: flex; flex-direction:row; flex-wrap:wrap; justify-content: space-between; }
.header_top.header_icon{width: 33%;} 
.header_bottom{width: 90%; margin: 0 auto; text-align: center;}
.header_icon:first-child>a{color: #000;}
/* 채팅창 */
.chat{ position: relative; top: 0px;}
.body-chat {padding-bottom: 0;  border-top: 1px solid rgba(0, 0, 0, 0.1);}
.chat-header,.chat-header a {background-color: #a1c0d6;color: black;}
.chat-header {border-bottom: 1px solid rgba(0, 0, 0, 0.1);}
.chat {background-color: #a1c0d6;height: 90vh;padding-top: 15px;padding-left: 5px;padding-right: 5px;}
.chat .date-divider {text-align: center;font-size: 12px;color: rgba(0, 0, 0, 0.5);margin-bottom: 15px;}
.chat__message {margin-bottom: 10px;display: flex;align-items: flex-end;}
.chat__message-from-me {justify-content: flex-end;text-align:right;}
.chat__message--to-me img {align-self: flex-start;}
.chat__message-time {font-size: 10px;color: rgba(0, 0, 0, 0.5);}
.chat__message-from-me .chat__message-body {background-color: #ffe934;margin-left: 5px;align-self:flex-end;}
.chat__message-body {align-self:flex-start;padding: 10px 5px;border-radius: 5px;background-color: #ffffff;margin-right: 5px;margin-top:5px;margin-bottom:5px;}
.left{text-align:left;}
.chat__message--to-me img {height: 35px;border-radius: 50%;margin-right: 10px;}
.chat__message .chat__message-username {font-size: 12px;font-weight: 600;margin-bottom: 5px;}
.chat__message-center {display: flex;flex-direction: column;}
.type-message {width: 100%;bottom: 0;position: fixed;height: 45px;background: #eeeeee;display: flex;align-items: center;justify-content:space-around;padding-left: 10px;padding-right: 5px;}
.type-message .fa-plus {color: #b4b4b4;}
.type-message__input {width: 80%;display: flex;align-items: center; position: relative;}
.type-message__input input {width: 100%;padding: 10px;}
.type-message__input .fa-smile-o {position: absolute;right: 40px;color: #b4b4b4;}
.type-message__input>i{position: absolute;right: 40px; color: #b4b4b4; font-size: 20px;}
.type-message__input .record-message {color: #523737;background-color: #ffe934;padding: 10px;cursor: pointer;}
