@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);

/* search-bar */
.search-bar{ height:20px; width: 100%; height: 60px; position: relative;}
.search-bar>input{position: absolute; top: 16px; left:30px; width: 90%; height: 30px; background-color: #eee; text-align: center; font-size: 13px; border-radius: 5px; transition: border-color .2s linear; border: 1px solid transparent; box-sizing: border-box;}
.search-bar input:focus {outline: none;border-color: #ffe94a;}
.search-bar i {z-index: 10;position: absolute;top:23px;left: 40px;color: #bbbbbb;}

/* global */
body{font-family: 'Open Sans', sans-serif; }

main{-webkit-animation: 0.5s mainmove ease-in-out;animation: 0.5s mainmove ease-in-out;}

@-webkit-keyframes mainmove{
    0%{
        opacity: 0;
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        opacity: 1;
    }
}

@keyframes mainmove{
    0%{
        opacity: 0;
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        opacity: 1;
    }
}
/* tab-bar */
.tab-bar{width:100%; background-color: #f7f7f7; position: fixed; bottom: 0; display: -webkit-flex; display: flex; -webkit-justify-content: space-between; justify-content: space-between; border-top: 1px solid #c1c1c1; padding:10px; box-sizing: border-box;}
.tab-bar_tab{ display: -webkit-flex; display: flex; -webkit-flex-direction:column; flex-direction:column; -webkit-align-items: center; align-items: center; color: #c1c1c1; transition: all .3s linear; font-size: 18px; text-decoration: none; padding-left: 20px; padding-right: 20px;}
.tab-bar a{display: block; text-decoration: none; display: -webkit-flex; display: flex; -webkit-align-items: center; align-items: center; -webkit-flex-direction: column; flex-direction: column; color:#c1c1c1; -webkit-transform: scale(1); transform: scale(1); transition: -webkit-transform 0.3s linear 0s; transition: transform 0.3s linear 0s; transition: transform 0.3s linear 0s, -webkit-transform 0.3s linear 0s;}

/* 클릭하면 바뀌게해주기. */
.tab-bar_tab:hover{ -webkit-transform: scale(1.1); transform: scale(1.1);}
.tab-bar .tab-bar_tab_on{color: #523737;}

.tab-bar_title{font-size: 10px;}
.tab-bar_tab>i{ margin-bottom: 3px; font-size: 20px;}






